import axios from "axios";
import jwt_decode from "jwt-decode";

const instance = axios.create({
    baseURL: process.env.REACT_APP_API
});

const authInterceptor = (config: any) => {
    config.headers.authorization = `Bearer ${localStorage.getItem('token')}`
    return config
}

instance.interceptors.request.use(authInterceptor)
export const getDetails = () => {
    return instance.get('/detail')
}
export const signUpUser = async (email: string, password: string) => {
    const {data} = await instance.post('/user/registration', {
        email, password
    })
    localStorage.setItem('token', data.token);
    return jwt_decode(data.token)
}

export const loginUser = async (email: string, password: string) => {
    const {data} = await instance.post('/user/login', {email, password})
    localStorage.setItem('token', data.token)
    return jwt_decode(data.token)
}

export interface IUser {
    email: string
}

export const checkAuth = async () => {
    const {data} = await instance.get('/user/auth' )
    localStorage.setItem('token', data.token)
    return jwt_decode<IUser>(data.token)
}

