import {Box, Button, Grid, IconButton, Link, TextField, Toolbar, Typography} from "@mui/material";
import batmanLogo from "../../assets/batman.png"
import logo from "../../assets/logo-small.jpeg"
import position from "../../assets/position.png"
import PersonIcon from '@mui/icons-material/Person';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import {LayoutInlinePaddings} from "../ui/LayoutInlinePaddings";
import './Header.scss'
import {useEffect, useState} from "react";
import {checkAuth, IUser} from "../../api/api";

const sections = [
    {title: 'Запчасти', url: '#'},
    {title: 'АКПП', url: '#'},
    {title: 'Двигатели', url: '#'},
    {title: 'МКПП', url: '#'},
    {title: 'Рулевые рейки', url: '#'},
    {title: 'Топливная система', url: '#'},
    {title: 'Турбины', url: '#'},
    {title: 'О нас', url: '#'},
    {title: 'Новости', url: '#'},
    {title: 'Контакты', url: '#'},
];

export const Header = () => {
    const [user, setUser] = useState<null | IUser>(null);
    const [isFetching, setIsFetching] = useState(true);

    const onExitClick = () => {
        localStorage.removeItem('token');
        setUser(null);
    }

    useEffect(() => {
        (async function () {
            try {
                const res = await checkAuth();
                setUser({email: res.email})
                setIsFetching(false);
            } catch (e) {
                console.log(e)
                setIsFetching(false);
            }
        })()
    }, [])
    console.log(user)
    return <div className={'header'}>
        <Box sx={{borderBottom: 1, borderColor: 'divider'}}>
            <LayoutInlinePaddings>
                <div className={'header__container'}>
                    <div className={'header__logo-wrapper'}>
                        <Grid item>
                            <img src={logo} alt="ГРМ моторс" width={100}/>
                        </Grid>
                        <Grid item container alignContent={'center'}>
                            <Grid item>
                                <img src={position} alt="Казань" width={20}/>
                            </Grid>
                            <Grid item>Казань</Grid>
                        </Grid>
                    </div>
                    <Grid container item xs flexDirection={'column'} wrap={'nowrap'}>
                        <Grid item>
                            <div className={'header__phone-time-wrapper'}>
                                <Grid item>
                                    <Link underline="hover" href="tel: +7 (843) 216-42-32">+7 (843) 216-42-32</Link>
                                </Grid>
                                <Grid item sx={{pl: 2}}>
                                    <Typography color='text.secondary'>Время работы: c 9-00 до 19-00</Typography>
                                </Grid>
                            </div>
                        </Grid>
                        <Grid container item sx={{pt: 1}} wrap={'nowrap'}>
                            <Grid item xs>
                                <TextField
                                    className={'header__text-field'}
                                    fullWidth
                                    size={'small'}
                                    id="outlined-search"
                                    label="Введите номер детали, название или VIN"
                                    type="search"
                                />
                            </Grid>
                        </Grid>
                    </Grid>
                    <div className={'header__registry-basket-wrapper'}>
                        <Grid container item sx={{width: 200}} wrap={'nowrap'} alignItems={'center'}>
                            <Grid item alignItems={'center'} sx={{width: 35, height: 35}}>
                                <PersonIcon fontSize={'large'}/>
                            </Grid>
                            {isFetching
                                ?
                                null
                                :
                                (user
                                        ?
                                        <Grid container item flexDirection={'column'} sx={{ml: 1}}>
                                            <Grid item>
                                                {user.email}
                                            </Grid>
                                            <Grid item>
                                                <Button onClick={onExitClick} size={'small'} disableFocusRipple color={'inherit'} >Выйти</Button>
                                            </Grid>
                                        </Grid>
                                        :
                                        <Grid container item flexDirection={'column'} sx={{ml: 1}}>
                                            <Grid item>
                                                <Link color={'text.secondary'} underline="hover"
                                                      href="/registration">Регистрация</Link>
                                            </Grid>
                                            <Grid item>
                                                <Link color={'text.secondary'} underline="hover"
                                                      href="/auth">Вход</Link>
                                            </Grid>
                                        </Grid>
                                )

                            }

                        </Grid>
                        <Button color={'inherit'} sx={{width: '60%', ml: 1}}>
                            <Grid container item wrap={'nowrap'} alignItems={'center'}>
                                <Grid item alignItems={'center'} sx={{width: 35, height: 35}}>
                                    <ShoppingCartIcon fontSize={'large'}/>
                                </Grid>
                                <Grid container item flexDirection={'column'} sx={{ml: 1}}>
                                    <Grid item>
                                        <Typography variant={'body1'} color='text.secondary'>В корзине 0 шт</Typography>
                                    </Grid>
                                    <Grid item>
                                        <Typography color='text.secondary'>на сумму 0 руб</Typography>
                                    </Grid>
                                </Grid>
                            </Grid>
                        </Button>
                    </div>
                </div>
            </LayoutInlinePaddings>
        </Box>
        <Toolbar
            component="nav"
            variant="dense"
            sx={{justifyContent: 'space-between', overflowX: 'auto'}}
        >
            <Link
                color="inherit"
                noWrap
                key='icon'
                variant="body2"
                href='#'
                sx={{p: 1, flexShrink: 0}}
            >
                <img src={batmanLogo} width={'50px'}/>
            </Link>
            {sections.map((section) => (
                <Link
                    color="inherit"
                    noWrap
                    key={section.title}
                    variant="body2"
                    href={section.url}
                    sx={{p: 1, flexShrink: 0}}
                >
                    {section.title}
                </Link>
            ))}
        </Toolbar>
    </div>
}